@import url(https://fonts.googleapis.com/css?family=Courgette&display=swap);
.react-tagsinput {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
  padding-left: 5px;
  padding-top: 5px;
  font-family: 'Courgette', cursive;
}

.react-tagsinput--focused {
  border-color: #ffb9c5;
}

.react-tagsinput-tag {
  background-color: #ffe3ed;
  border-radius: 2px;
  border: 1px solid #ffb9c5;
  color: #3c3f41;
  display: inline-block;
  font-family: 'Courgette', cursive;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 2px;
}

.react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}

.react-tagsinput-tag a::before {
  content: " ×";
}

.react-tagsinput-input {
  background: transparent;
  border: 0;
  color: #777;
  font-family: 'Courgette', cursive;  font-size: 13px;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 5px;
  margin-top: 1px;
  outline: none;
  padding: 2px;
  width: 80px;
}

